  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  line-height: 150%;
  //box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
  transition: 0.5s ease-in-out;
}

// Прячем скроллбар
::-webkit-scrollbar {
  width: 0;
  background: transparent;
}

html {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

body {
  overflow-x: hidden;
  width: 100%;
}
