// Animation

@keyframes spin {
  0% {
    transform:rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

@keyframes orbit--sm {
  0%,
  to {
    transform: translate(0%, 0%)
  }
  25%,
  75% {
    transform: translate(-2.5%, 7.5%)
  }
  50% {
    transform: translate(-5%, 3.5%)
  }
}

@keyframes orbit--lg {
  0%,
  to {
    transform: translate(0%, 0%)
  }
  25%,
  75% {
    transform: translate(-2.5%, 10%)
  }
  50% {
    transform: translate(-10%, 5%)
  }
}

@keyframes innerOrbit {
  0% {
    transform: rotate(0deg)
  }
  to {
    transform: rotate(360deg)
  }
}

@keyframes innerOrbit2 {
  0% {
    transform: rotate(360deg)
  }
  to {
    transform: rotate(0deg)
  }
}

@keyframes psPlusFlip {
  0% {
    transform: rotateY(180deg)
  }
  50% {
    transform: rotateY(90deg)
  }
  to {
    transform: rotateY(0deg)
  }
}

@keyframes fFadeIn {
  0% {
    opacity: 0;
    transform: translate3d(0, -35%, 0);
    transition-timing-function: ease-out
  }
  to {
    opacity: 1;
    transform: none;
    transition-timing-function: ease-in
  }
}

// Gradient Background

@keyframes gradient {
  0%, 100% {
    background-position: 10% 0;
  }
  50% {
    background-position: 91% 100%;
  }
}

.in-view {
  animation:spin 4s linear;
}

.orbit{
  animation: innerOrbit 120s ease-out infinite;
}
.orbit_rev {
  animation: innerOrbit2 120s ease-out infinite ;
}
