  }
  .main_our_works_img_wrapper{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 5vh 0;
    .main_gallery {
      display: grid;
      grid-template-columns: repeat(4, 1fr);
      grid-template-rows: repeat(3, 1fr);
      grid-gap: 15px;
      height: 100%;
      justify-content: space-between;
      margin-bottom: 15px;

      .gallery_item {
        position: relative;
        height: 100%;
      }

      .gallery_item:hover .gallery_hover {
        opacity: 0;
      }
      .gallery_item img {
        width: 100%;
        height: 100%;
      }

      .thumb {
        position: relative;
        cursor: pointer;
        transform: scale(1);
        transition: transform 250ms ease-in-out;
      }
      .img-container {
        position: relative;
        margin: 0;
        width: 100%;
        height: 100%;
      }

      .img-container-bg {
        opacity: 0;
        visibility: hidden;
        position: fixed;
        z-index: 9;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background-color: rgba(0,0,0,0.5);
        transition-property: opacity, visibility;
        transition-duration: 350ms;
        transition-timing-function: linear;
      }
      // Временно отключенный ховер

      .gallery_hover {
        opacity: 0;
      }
      .img-container.is-active .img-container-bg {
        opacity: 1;
        visibility: visible;
      }
      .img-container.is-active .thumb {
        z-index: 99;
        transform: scale(1.8);
      }
      .img-container:hover::after {
        opacity: 1;
      }
      .gallery_hover {
        opacity: 0;
        transition: opacity .3s linear;
        background: #fff;
        font-size: 1.6rem;
        position: absolute;
        pointer-events: none;
        padding: 1rem;
        top: 0;
        left: 0;
      }
    }
    .hidden_g {
      display: none;
      transition: all 500ms;
    }
    .visible_g{
      display: grid;
      transition: all 500ms;
    }
    .long {
      grid-row: span 2;
    }
  }
}


